<template>
  <HomeTop/>
  <HomeBanner :banners="banners"/>
  <HomeIconList/>
  <HomeMusicMenu :musicMenu="musicMenu"/>
</template>

<script>
import HomeTop from '@/views/Home/HomeTop'
import HomeBanner from '@/views/Home/HomeBanner'
import HomeIconList from '@/views/Home/HomeIconList'
import HomeMusicMenu from '@/views/Home/HomeMusicMenu'
import {useStore} from 'vuex'
import {computed, reactive, toRefs} from 'vue'
import {reqBanners, reqMusicMenu} from '@/api'
export default {
  name: 'Home',
  components: {
    HomeTop, HomeBanner, HomeIconList, HomeMusicMenu
  },
  setup() {
    //使用vuex
    // const store = useStore()
    // //获取banner组件所需数据
    // // 创建时发送请求
    // store.dispatch('Home/getBanners')
    // // 使用计算属性获取数据
    // const banners = computed(() => store.state.Home.banners)
    //
    // //获取musicMenu组件所需数据
    // // 创建时发送请求
    // store.dispatch('Home/getMusicMenu')
    // // 使用计算属性获取数据
    // const musicMenu = computed(() => store.state.Home.musicMenu)

    const asyncData = reactive({
      banners: [],
      musicMenu: [],
    })
    reqBanners().then(res => {
      // console.log(res.banners)
      asyncData.banners = res.banners
    })
    reqMusicMenu().then(res => {
      // console.log(res.result)
      asyncData.musicMenu = res.result
    })
    return {
