<template>
  <div class="home-music-menu">
    <div class="music-top">
      <div class="title">发现好歌单</div>
<!--      <div class="more">查看更多</div>-->
    </div>
    <div class="music-content">
      <van-swipe
          :loop="false"
          :width="150"
          :show-indicators="false"
          class="my-swipe"
      >
        <van-swipe-item
            v-for="item in musicMenu"
            :key="item.id"
            @click="toRoute(item.id)"
        >
          <img :src="item.picUrl" :alt="item.name"/>
          <span class="play-count">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-yunhang"></use>
            </svg>
            {{ changeCount(item.playCount) }}
          </span>
          <span class="name">{{ item.name }}</span>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
import {useRouter} from 'vue-router'

export default {
  name: 'HomeMusicMenu',
  props: {
    musicMenu: Array,
  },
  setup() {
    //使用vue-router
    const router = useRouter()

    // 修改单位万、亿
    function changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(2) + '亿'
      } else if (num >= 10000) {
        return (num / 10000).toFixed(2) + '万'
      }
      return num
    }

    // 跳转详情页
    function toRoute(id) {
      router.push({
        name: 'MusicDetail',
        query: {
          id,
        },
      })
    }

    return {
      changeCount,
      toRoute,
    }
  },
}
</script>

<style lang="less" scoped>
.home-music-menu {
  width: 100%;
  padding: 0 0.2rem;
  height: 4rem;
  .music-top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.2rem;

    .title {
      font-weight: bold;
      font-size: 0.4rem;
    }

    .more {
      border-radius: 20px;
      padding: 5px;
      border: 1px solid #555;
    }
  }

  .music-content {
    width: 100%;

    .my-swipe {
      padding: 0 10px;

      .van-swipe-item {
        padding: 0.1rem;
        position: relative;
        .play-count {
          position: absolute;
          display: flex;
          align-items: center;
          color: white;
          top: 10px;
          right: 10px;
          font-size: .1rem;
          padding: 5px;
          background-color: rgba(0,0,0,.3);
          border-radius: 20px;
          .icon {
            fill: white;
          }
        }
        .name {
          font-size: .3rem;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
          padding: 6px 2px 0 6px;
          min-height: 30px;
        }
        img {
          border-radius: 10px;
          width: 100%;
        }
      }
    }
  }
}
</style>
