<template>
  <van-popup class="share" v-model:show="showShare" :style="{padding: '.2rem',}">
    <van-image
        width="5rem"
        height="5rem"
        fit="contain"
        :src="shareImg"
    >
      <template v-slot:loading>
        <van-loading type="spinner" size="20"/>
      </template>
      <template v-slot:error>
        <p @click="getshareImg">加载失败，点击重试</p>
      </template>
    </van-image>
    <p>分享该网站</p>
  </van-popup>
  <div class="top-nav">
    <div class="top-left">
      <svg class="icon" aria-hidden="true" @click="showShare = true">
        <use xlink:href="#icon-fenxiang"></use>
      </svg>
    </div>
    <div class="top-content">
      <!--      <span @click="$router.push('/UserInfo')" :class="{active: $route.name === 'UserInfo'}">我的</span>-->
      <span class="active" :class="{active: $route.name === 'Home'}">发现</span>
    </div>
    <div class="top-right">
      <svg class="icon" aria-hidden="true" @click="$router.push('/Search')">
        <use xlink:href="#icon-sousuo"></use>
      </svg>
    </div>
  </div>
</template>

<script>
import {ref, watch} from 'vue'

export default {
  name: 'HomeTop',
  setup() {
    const showShare = ref(false)
    const shareImg = ref('')
    watch(showShare, () => {
      getshareImg()
    })

    function getshareImg() {
      if (showShare.value === true) {
        shareImg.value = `https://api.pwmqr.com/qrcode/create/?url=${location.href}`
      }
    }

    return {
      showShare, //是否显示分享
      shareImg, //二维码图片路径
      getshareImg //获取二维码图片（防止加载失败）
    }
  }
}
</script>

<style lang="less" scoped>
.share {
  p {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: .6rem;
  }
}

.top-nav {
  width: 100%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .top-content {
    width: 65%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    font-size: 0.32rem;

    .active {
      font-weight: bold;
    }
  }
}
</style>
